* {
    box-sizing: border-box;
}

html {
    font: calc(100% * 0.625) / 1.15 HelveticaNeue, Helvetica, sans-serif;
}

body {
    font-size: 1.8rem;
    line-height: 1.875;
    color: #252525;
    margin: 0;
}

header {
    background-color: #252525d2;
    color: #fff;
    padding: 2rem;
}

header>h1,
main {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

legend>h3 {
    margin: 0;
}

main {
    width: 95%;
    max-width: 120rem;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
}

.main__form {
    flex: 4;
}

.feature {
    padding: 0.8rem;
    padding-top: 0.4rem;
    border: 0;
}

.feature+.feature {
    margin-top: 0.8rem;
}

.feature__name {
    font-size: inherit;
    line-height: inherit;
    font-weight: bold;
}

.feature__item+.feature__item {
    margin-top: 0.8rem;
}

/* Hide radios from sighted users,
  while keeping them available to the keyboard */
.feature__option {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px);
    /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0 !important;
    border: 0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

.feature__label {
    display: block;
    padding: 0.8rem;
    border: 0.1rem solid #d6d6d6;
    border-radius: 0.4rem;
}

.feature__label:hover {
    border-color: #888;
}

.feature__option:checked+.feature__label {
    border: 0.2rem solid #0070c9;
}

.feature__option:focus+.feature__label {
    box-shadow: 0 0 0 0.1rem #fff, 0 0 0 0.3rem #83c0fd;
}

.feature__option:not(:checked)+.feature__label {
    cursor: pointer;
}

.main__summary {
    flex: 3;
    margin-left: 0.2rem;
}

.summary__option {
    display: grid;
    grid-template-areas:
        'key cost'
        'value value';
    padding: 0.2rem;
    margin-top: 1rem;
}

.summary__option__label {
    font-size: 2.2rem;
    font-weight: bold;
    grid-area: key;
}

.summary__option__value {
    grid-area: value;
}

.summary__option__cost {
    grid-area: cost;
    text-align: right;
}

.summary__total {
    font-size: 2.2rem;
    line-height: 2.4;
    display: flex;
    margin-top: 1.5rem;
    border-top: solid 1px #252525;
}

.summary__total__label {
    font-weight: bold;
    flex: 2;
}

.summary__total__value {
    text-decoration: underline;
}
